#navbarCollapse {
  display: flex !important;
  align-items: center;
  width: 100%;
  color: #fff;
  height: 75px;
  box-shadow: 0 4px 12px 0 rgb(0 0 0 / 10%);
  /* background-color: #1db954; */
  /* background-color: #e2e2e2; */
  transition: background-color 0.2s, transform 0.3s 0.1s;
  background: #fff;
  border-bottom-color: #e6ebf0;
}
body {
  background-color: #fcfaf5 !important;
}
#headerWrapper {
  height: 75px;
  /* background-color: #fff; */
  position: fixed;
  width: 100%;
  z-index: 99999;
}
.mainLogoImage {
  max-width: 100px;
}
.logo-section p {
  /* color: #000; */
  cursor: pointer;
}
.logo-section span {
  margin-top: 0px;
  display: block;
}
.logo-section {
  font-size: 22px;
  /* color: #2d3092; */
  font-style: italic;
  font-weight: 600;
  cursor: pointer;
}
#navbarCollapse ul {
  flex-direction: row;
  /* margin-right: auto !important;
    margin-left: auto !important;
    display: flex;
    flex-direction: column;
    padding-left: 0;
    margin-bottom: 0;
    list-style: none; */
}
.leftsideItem img {
  cursor: pointer;
  background: #fff;
  border-radius: 4px;
}
.signInButtonArea {
  margin-left: 25px;
}
.signInButton {
  background-color: #fff;
  color: #0a0a0a;
  border: none;
  height: 45px;
  padding: 5px 0px;
  border-radius: 4px;
  margin: 0px 20px;
  padding-top: 13px;
}
#navbarCollapse ul li {
  display: inline;
  padding: 0px 20px;
}
.centersideItem input {
  width: 300px;
  height: 38px;
  border: 1px solid #ced4da;
  border-radius: 4px;
  padding-left: 10px;
}
.cart_items_count {
  position: absolute;
  margin-top: -5px;
  color: #222020;
}
.rightsideItem > span {
  cursor: pointer;
}
#navbarCollapse ul li a {
  text-decoration: auto;
  color: #000;
}
.headerSection {
  /* background-color: #ffffff; */
}
.allItemsSection {
  width: 90%;
  margin: auto;
  display: flex;
  justify-content: space-between;
}
.allItemsSection > div {
  text-align: center;
  /* height: 70px; */
}
.cartIconPage {
  margin-top: 4px;
}

.leftsideItem {
  /* width: 20%; */
}
.rightsideItem {
  /* width: 60%; */
  display: flex;
  justify-content: end;
}
.rightsideItem {
  display: flex;
  padding-top: 5px;
}
.centersideItem {
  /* width: 20%; */
}

.profileIconStyles {
  width: 38px;
  height: 38px;
  background-color: #ccc;
  color: #fff;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
}

.menuDropdownMainSection {
  border: 1px solid #e2e2e2;
  border-radius: 4px;
  width: 200px;
  background-color: #fff;
  padding: 10px 20px;
}
.menuDropdownMainSection .items-list {
  cursor: pointer;
  margin: 10px 0px;
  padding: 5px 0px;
}
.Popover-body .menuDropdownMainSection > div:not(.items-list-signout) {
  border-bottom: 1px solid #e2e2e2;
}
.signInButton.selected {
  border-bottom: 1px solid #0a0a0a;
  line-height: 19px;
  border-radius: 0px;
  /* background-color: #5858e1; */
  /* color: #fff; */
}
.rightsideItem .signInButton:hover {
  border-bottom: 1px solid #fff;
  line-height: 19px;
  border-radius: 0px;
}
.navbar-collapse.collapse.show {
  width: 300px;
  background: #fff;
}
.navbar-collapse.collapse.show .navbar-nav {
  width: 300px;
  background: #fff;
}
.navbar-collapse.collapse.show a {
  font-size: 20px;
  font-weight: 600;
  color: #e5e3dc;
}
@media (max-width: 800px) {
  .centersideItem input {
    width: 85%;
  }
  .leftsideItem {
    width: 15%;
  }
  .rightsideItem {
    background: #fff;
    width: 79px;
    border-radius: 4px;
  }

  .centersideItem {
    width: 5%;
  }
  .allItemsSection {
    width: 90%;
    flex-direction: row-reverse;
  }
  .signInButton {
    padding: 5px 10px;
  }
  .mainLogoImage {
    max-width: 70px;
  }
}
.fin-tech-apply-now-form {
  top: 10px !important;
  border-radius: 10px;
}

.fin-tech-apply-now-form .modal-dialog {
  max-width: 600px;
}
