.home-products-section{
    max-width: 1020px;
    margin: auto;
}
.all-products-list{
    display: flex;
    display: -webkit-flex;
    flex-wrap: wrap;
    -webkit-flex-wrap: wrap;
    padding: 16px 0px;
 
}
.home-products-section .products-list {
    max-width: 1020px;
}
.sorting-section{
    margin-top: 30px;
}
.sorting-section .dropdown{
    padding: 0px;
    
}
.sorting-section .sort-sec{
    width: 250px;
}
.products-header{
    display: inline-block;
    width: 100%;
    margin-top: 30px;
}
.sorting-section{
    display: flex;
    border-bottom: 1px solid #e2e2e2;
    padding-bottom: 7px;
    /* float: right; */
}
.sorting-section .order-filter-date .btn{
    padding: 10px 17px;
    border-radius: 4px;
}

.all-sorting-option ul{
    padding: 0px;
    margin: 0px;
}
.all-sorting-option ul li{
    display: inline-block;
}
.all-sorting-option ul li:not(:first-child) {
    margin-left: 30px;
}
.all-sorting-option ul li span{
    cursor: pointer;
    
}
.all-sorting-option ul li span:hover, .all-sorting-option ul li.active span{
    border-bottom: 2px solid #1db954;
    padding-bottom: 8px;
    color: #1db954;
}

.all-category-option{
    margin-top: 20px;
}
.all-category-option ul{
    padding: 0px;
    margin: 0px;
}
.all-category-option ul li{
    display: inline-block;
    height: 50px;
}
.all-category-option ul li:not(:first-child) {
    /* margin-left: 30px; */
}
.all-category-option ul li span{
    cursor: pointer;
    margin-right: 30px;
    border: 1px solid #e2e2e2;
    border-radius: 50px;
    padding: 7px 22px;
    font-size: 14px;
    color: #555454;
}
/* .all-category-option ul li span:hover,  */
.all-category-option ul li.active span{
    background: #1db954;
    color: #fff;
}
.page-loader-main{
    margin-top: 100px;
}