.product-upload-section{
    width: 70%;
    margin: auto;
    
    border: transparent;
    margin-top: 50px;
    border-radius: 4px;
    box-shadow: 1px 1px 5px rgb(0 0 0 / 8%);
    background: #fff;
}
.product-upload-body{
    padding: 50px 30px;
}
.product-upload-head{
    background-color: rgba(0,0,0,.03);
    border-bottom: 1px solid rgba(0,0,0,.125);
    padding: 15px;
}
.singleRowSection {
}
.singleRowSection .price_btn_sm{
    width: 17%;
    /* float: left; */
}
.priceSection {
    margin-top: 35px;
}
.singleRowSection .actionRowItem{
    width: 2%;
    padding-top: 35px;
}
.singleRowSection .price_btn_lm{
    width: 28%;
    /* float: left; */
}
.singleRowSection{
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.submit-btn-section{
    text-align: center;
    margin-top: 50px;
}
.submit-btn-section button {
    width: 200px;
    height: 50px;
}
.imageContainer {
    background: #dcd8d8;
    border: 1px dashed #4e4e4e;
    border-radius: 9px;
    height: 256px;
    margin: 22px 66px 43px!important;
    align-items: center;
    width: 80%;
    justify-content: center;
}
.imageContainer label {
    display: inline-block;
    margin-bottom: 0.5rem;
}
.imageRow img{
    max-height: 200px;
}
.form-group .invalid-feedback{
    display: block;
}
.add-new-price-btn-section Button{
    background-color: #7184b1;
    border-color: #7184b1;
}