.cart-left-side-continer{
    width: 70%;
    float: left;
    padding-top: 30px;
    margin-bottom: 250px;
}
.cart-right-side-continer{
    width: 30%;
    float: left;
    padding-top: 70px;
}

.product_cart_header{
    background: #fff;
    /* margin: 16px 0px; */
    width: 100%;
    height: 20px;
    border-radius: 2px;
    height: 46px;
    text-align: center;
    float: left;
    justify-content: space-between;
    display: flex;
    padding-top: 9px;
}
.freshbag-container{
    width: 100%;
    margin: auto;
   
}
.products-list-cart{
    
    display: grid;
    /* display: flex; */
    /* display: -webkit-flex; */
    flex-wrap: wrap;
    -webkit-flex-wrap: wrap;
    /* padding: 16px; */
    max-width: 960px;
    margin: 0 auto;
}
.products-list-cart-cart{
    border: 1px solid #e2e2e2;
}
.products-list-cart-cart > p{
    padding: 10px;
}
.products-list-cart-cart .product_cart, .products-list-cart-cart .product_cart_header{
    border-top: 1px solid #e2e2e2;
}
.text_header_cart{
    font-size: 20px;
    font-weight: 400;
    padding-right: 10px;
}
.text_count_cart{
    color: #9e9e9e;
}
.summary-list-cart{
    width: 90%;
    margin: auto;
    background: #fff;
    border: 0.5px solid #d3d3d3;
    /* padding: 20px; */
}
.cart-summary-header p {
    margin: 0px;
}
.cart-summary-header{
    padding: 15px;
    border-bottom: 1px solid #e2e2e2;
}
.summary-cart-item{
    justify-content: space-between;
    display: flex;
    padding: 10px 0px;
}
.summary-cart-items{
    width: 85%;
    margin: auto;
}
.summary-cart-items .summary-cart-item:not(:last-child) {
    border-bottom: 1px solid #e2e2e2;
}

.procced_checkout{
    text-align: center;
    margin-top: 50px;
}
.procced_checkout button {
    color: #FFF;
    background-color: #25a541;
    border: none;
    height: 45px;
    width: 90%;
    /* padding: 0px 30px; */
    border-radius: 4px;
}
.amount_count_cart:before {
    content: "₹ ";
}